import React from 'react'
import mapboxgl from 'mapbox-gl'
import './Map.css';

mapboxgl.accessToken = 'pk.eyJ1IjoibWFwYm94IiwiYSI6ImNpejY4M29iazA2Z2gycXA4N2pmbDZmangifQ.-g_vE53SD2WrJ6tFX7QHmA';

class Map extends React.Component {

  constructor(props: Props) {
    super(props);
    this.state = {
      'latitude' : 56.640977,
      'longitude' : 47.851050,
      'accuracy' : 0,
      zoom : 12
    };
  }

  componentDidUpdate(prevProps) {
    const latitude = this.props.latitude;
    const longitude = this.props.longitude;

    if (latitude !== prevProps.latitude || longitude !== prevProps.longitude) {
      if(typeof this.map.getLayer('0') !== 'undefined') {
        this.map.removeLayer('0').removeSource({
            "type": "geojson",
            "data": {
                "type": "FeatureCollection",
                "features": [{
                    "type": "Feature",
                    "geometry": {
                        "type": "Point",
                        "coordinates": [prevProps.longitude, prevProps.latitude]
                    },
                    "properties": {
                        "title": "",
                        "icon": "circle"
                    }
                }]
            }
        });
        console.log(this.map.getLayer());
      }
      this.map.flyTo({
          center: [
            this.props.longitude,
            this.props.latitude],
          zoom: 18
      });

      this.map.addLayer({
        "id": "0",
        "type": "symbol",
        "source": {
            "type": "geojson",
            "data": {
                "type": "FeatureCollection",
                "features": [{
                    "type": "Feature",
                    "geometry": {
                        "type": "Point",
                        "coordinates": [longitude, latitude]
                    },
                    "properties": {
                        "title": "",
                        "icon": "circle"
                    }
                }]
            }
        },
        "layout": {
            "icon-image": "{icon}-15",
            "text-field": "{title}",
            "text-font": ["Open Sans Semibold", "Arial Unicode MS Bold"],
            "text-offset": [0, 0.6],
            "text-anchor": "top"
        }
      });
    }
  }

  componentDidMount() {
    let {latitude, longitude, accuracy, zoom} = this.state;
    this.map = new mapboxgl.Map({
      container: this.mapContainer,
      style: 'mapbox://styles/mapbox/streets-v9',
      center: [longitude, latitude],
      zoom: zoom
    });

   this.map.on( 'load', () => this.map.addControl(new mapboxgl.NavigationControl()) );
  }

  render() {
    return (
      <div ref={el => this.mapContainer = el} className='Map' />
    );
  }
}

export default Map